import Vue from 'vue'
import VueRouter from 'vue-router'
import './assets/css/tailwind.css'

import App from './App.vue'
import Home from './pages/Home.vue'
import Musab from './pages/Musab.vue'
import PhotoMemory from './pages/PhotoMemory.vue'

Vue.config.productionTip = false

Vue.use(VueRouter)

const routes = [
  { path: '/', component: Home},
  { path: '/musab', component: Musab},
  { path: '/photo-memory', component: PhotoMemory}
];

const router = new VueRouter({
  routes,
  mode:'history'
});


new Vue({
  router,
  render: h => h(App),
}).$mount('#app')
